import React from "react";
import { Link } from "react-router-dom";

export default function Post() {
	return (
		<div className="post">
			<div className="image">
				<img
					src="https://avatars.cloudflare.steamstatic.com/0fcc1ae86b1e2c92d36688e78ee938ca68738f9c_full.jpg"
					alt="profile"
				/>
			</div>
			<div className="texts">
				<h2>Blog Title with topic gist</h2>
				<p className="info">
					<Link className="author">Sambhav Aswal</Link>
					<time>2023-01-06 3:27</time>
				</p>
				<p className="summary">
					This is a long sentence. It is lengthy and interesting to read. Might
					have a bunch of paragraphs and what not. It must have a lot of stuff
					to say, it is a long sentence after all.
				</p>
			</div>
		</div>
	);
}
